
:root{
--select-color: #00ACDC;
}

body{
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 12px;
  margin: 0;
  padding: 0;
}

.menu {
  display: flex;
  align-items: center;
  height: 60px;
  margin: 0 30px;
  justify-content: space-between;
}

.menu-items {
  display: flex;
  list-style: none;
}

.list{
  text-decoration: none;
  color: #000;
  margin: 0 20px;
  text-transform: uppercase;

}

.data-qa:hover {
  color: var(--select-color);
}

.is-active {
  position: relative; /* Встановлюємо позицію для псевдоелемента */
}

.is-active::after {
  content: ""; /* Створюємо псевдоелемент */
  display: block; /* Робимо його видимим */
  position: absolute; /* Абсолютне позиціонування */
  bottom: -10px;
  border-radius: 8px;
  left: 0; /* Початок лінії з лівого краю */
  width: 100%; /* Лінія покриває всю ширину елемента */
  height: 3px; /* Товщина лінії */
  background-color: var(--select-color); /* Чорний колір лінії */
}



